<template>
<div>
  <div style="height:100%; margin-bottom: 60px;">
    <div>
      <div class="header"></div>
      <van-field
        class="readOnly"
        required
        readonly
        label=":"
        v-model="baseInfo.businessModel == '1' ? '' : '' "
      />

      <div>
        <div class="zlhjRadio" style="display:flex">
          <span class="zlhjRadio_title">GPS</span>
          <div class="zlhjRadio_body">
            <div
              @click="baseInfo.hasGps = '1'"
              :class="baseInfo.hasGps == '1' ? 'zlhjRadio_body_item_selected' : 'zlhjRadio_body_item'"
            ></div>
            <div
              @click="baseInfo.hasGps = '2'"
              :class="baseInfo.hasGps == '2' ? 'zlhjRadio_body_item_selected' : 'zlhjRadio_body_item'"
            ></div>
          </div>
        </div>
        <div style="padding-left:10px">
          <div class="zlhjRadioLine"></div>
        </div>
      </div>
      <div>
        <div class="zlhjRadio" style="display:flex">
          <span class="zlhjRadio_title"></span>
          <div class="zlhjRadio_body">
            <div
              @click="baseInfo.isCs = '1'"
              :class="baseInfo.isCs == '1' ? 'zlhjRadio_body_item_selected' : 'zlhjRadio_body_item'"
            ></div>
            <div
              @click="baseInfo.isCs = '2'"
              :class="baseInfo.isCs == '2' ? 'zlhjRadio_body_item_selected' : 'zlhjRadio_body_item'"
            ></div>
          </div>
        </div>
        <div style="padding-left:10px">
          <div class="zlhjRadioLine"></div>
        </div>
      </div>
      <div>
        <div class="zlhjRadio" style="display:flex">
          <span class="zlhjRadio_title"></span>
          <div class="zlhjRadio_body">
            <div
              @click="baseInfo.isJjk = '1'"
              :class="baseInfo.isJjk == '1' ? 'zlhjRadio_body_item_selected' : 'zlhjRadio_body_item'"
            ></div>
            <div
              @click="baseInfo.isJjk = '2'"
              :class="baseInfo.isJjk == '2' ? 'zlhjRadio_body_item_selected' : 'zlhjRadio_body_item'"
            ></div>
          </div>
        </div>
        <div style="padding-left:10px">
          <div class="zlhjRadioLine"></div>
        </div>
      </div>
      <div class="headerAndBtn">
        <div class="header"></div>
        <div style="width:40px;margin-top:18px;" @click="showAddCost = true">
          <van-icon color="#ff9900" size="20px" name="add-o" />
        </div>
      </div>
      <div class="costBox">
        <div
          style="display:inline-block;margin:0 8px"
          v-for="(item, index) in cosList"
          :key="index"
        >
          <div class="costBox_item">
            <div class="costBox_item_body">
              <div>: {{getValue(item.expenseType,cosTypeList)}}</div>
              <div style="margin-top:5px">: {{item.amount}} </div>
            </div>
            <div v-if="item.expenseType != '1'" class="costBox_item_action">
              <van-button style="height:100%;width:100%;" type="info" @click="delectCost(item)"></van-button>
            </div>
            <div v-else class="costBox_item_action">
              <van-button style="height:100%;width:100%;" type="info">-</van-button>
            </div>
          </div>
        </div>
      </div>
      <div class="header"></div>
      <van-field
        class="readOnly"
        required
        v-show="baseInfo.businessModel == '1'"
        readonly
        label=":"
        v-model="baseInfo.leaseTypeName"
      />
      <van-field
        class="readOnly"
        v-show="baseInfo.businessModel == '1'"
        type="digit"
        label="():"
        v-model="totalAmount"
        placeholder=""
      />
      <van-field
        class="readOnly"
        v-show="baseInfo.businessModel == '2'"
        type="digit"
        label="():"
        v-model="totalAmount"
        placeholder=""
      />
      <van-field
        label="():"
        type="digit"
        v-model="baseInfo.downPaymentAmount"
        placeholder=""
      />
      <van-field
        v-show="baseInfo.businessModel == '1'"
        class="readOnly"
        label="():"
        type="digit"
        placeholder=""
        v-model="amount"
      />
      <van-field
        v-show="baseInfo.businessModel == '2'"
        class="readOnly"
        label="():"
        type="digit"
        placeholder=""
        v-model="amount"
      />
      <van-field
        is-link
        readonly
        clickable
        v-show="baseInfo.businessModel == '2'"
        label="():"
        v-model="baseInfo.term"
        placeholder=""
        @click="showTerm = true"
      />
      <van-field
        is-link
        readonly
        clickable
        v-show="baseInfo.businessModel == '1'"
        label="():"
        v-model="baseInfo.term"
        placeholder=""
        @click="showTerm = true"
      />
      <van-field
        is-link
        readonly
        clickable
        label=":"
        v-model="baseInfo.repaymentMethodName"
        placeholder=""
        @click="showRepaymentMethod = true"
      />
      <van-field
        is-link
        readonly
        clickable
        v-show="baseInfo.businessModel == '1'"
        label=":"
        v-model="baseInfo.useMethodValue"
        placeholder=""
        @click="showUseMethod = true"
      />
      <van-field
        is-link
        readonly
        clickable
        v-show="baseInfo.businessModel == '2'"
        label=":"
        v-model="baseInfo.useMethodValue"
        placeholder=""
        @click="showUseMethod = true"
      />
      <van-field
        class="readOnly"
        v-show="baseInfo.businessModel == '1'"
        label="(%):"
        type="digit"
        v-model="rate"
        placeholder=""
      />
      <van-field
        class="readOnly"
        v-show="baseInfo.businessModel == '2'"
        label="(%):"
        type="digit"
        v-model="rate"
        placeholder=""
      />
      <van-field
        class="readOnly"
        label="GPS():"
        type="digit"
        v-model="gpsAmount"
        placeholder=""
      />
      <div class="header"></div>
      <van-field
        class="readOnly"
        label=":"
        v-model="baseInfo.managerName"
        placeholder=""
      />
      <van-field
        class="readOnly"
        label=":"
        v-model="baseInfo.managerMobileNumber"
        placeholder=""
      />
    </div>

  </div>
      <div class="subBtn">
      <van-button class="subBtn_body" block type="info" @click="toSub"> </van-button>
    </div>
    <div>
      <van-popup
        v-model="showTerm"
        position="bottom"
        :style="{ height: '300px', width: '100%'}"
        get-container="body"
      >
        <van-picker
          title
          show-toolbar
          :columns="termList"
          @confirm="selectTerm"
          @cancel="showTerm=false"
        />
      </van-popup>
      <van-popup
        v-model="showRepaymentMethod"
        position="bottom"
        :style="{ height: '300px', width: '100%'}"
        get-container="body"
      >
        <van-picker
          title
          show-toolbar
          :columns="repaymentMethodList"
          @confirm="selectRepayment"
          @cancel="showRepaymentMethod=false"
        />
      </van-popup>
      <van-popup
        v-model="showUseMethod"
        position="bottom"
        :style="{ height: '300px', width: '100%'}"
        get-container="body"
      >
        <van-picker
          title
          show-toolbar
          :columns="getPickData(this.useTypeList)"
          @confirm="selectUseMethod"
          @cancel="showUseMethod=false"
        />
      </van-popup>

      <van-popup
        v-model="showAddCost"
        :style="{ height: '220px', width: '90%'}"
        get-container="body"
      >
        <div>
          <div class="showAddCost_title van-hairline--bottom"></div>
          <div class="showAddCost_body">
            <van-field
              readonly
              clickable
              is-link
              style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
              label=":"
              v-model="addCostTypeValue"
              placeholder=""
              @click="showCosType = true"
            />
            <van-field
              style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
              label=":"
              type="digit"
              placeholder=""
              v-model="addCostAmount"
            />
          </div>
          <div class="showAddCost_btn van-hairline--top">
            <van-button style="width:50%" @click="showAddCost = false"></van-button>
            <van-button style="width:50%" type="info" @click="addCostAction"></van-button>
          </div>
        </div>
      </van-popup>
      <van-popup
        v-model="showCosType"
        position="bottom"
        :style="{ height: '300px', width: '100%'}"
        get-container="body"
      >
        <van-picker
          title
          show-toolbar
          :columns="getPickData(this.cosTypeList)"
          @confirm="selectCosType"
          @cancel="showCosType=false"
        />
      </van-popup>
    </div>
</div>

</template>

<script>
import {
  BusinessInfo,
  CostList,
  CalculateCost,
  addCost,
  delCost,
  saveBussinessInfo,
  codeList
} from "../../request/api";
export default {
  data() {
    return {
      addCostAmount: "",
      addCostType: "",
      addCostTypeValue: "",
      addCostId: 0,
      showTerm: false,
      isHaveGPS: false, // GPS
      showAddCost: false,
      showRepaymentMethod: false,
      showUseMethod: false,
      showCosType: false,
      cosTypeList: [],
      useTypeList: [],
      termList: ["12", "24", "36"],
      repaymentMethodList: ["", "", ""],
      totalAmount: 0, // 
      carAmount: 0,
      rate: 0, //
      gpsAmount: 0, //GPS

      baseInfo: {
        loanNumber: "", // 
        managerName: "", //
        managerMobileNumber: "", //
        carPrice: 0, //
        businessModel: "1", // 1-2-
        leaseType: "2", // 1-2-
        leaseTypeName: "", //
        carPrice: "", //
        hasGps: "", //GPS 1-2-
        isCs: "", // 1-2-
        isJjk: "", //  1-2-
        downPaymentAmount: 0, //
        term: 0, //
        repaymentMethod: "", // 1-2-3-
        repaymentMethodName: "", // 1-2-3-
        useMethod: "", // -
        useMethodValue: "" // 
      },
      amount: 0, //
      cosList: [] //
    };
  },
  watch: {
    "baseInfo.downPaymentAmount": {
      deep: true,
      handler: function(newValue, oldValue) {
        console.log(newValue, oldValue);
        this.calculateCost();
      }
    },
    showAddCost: {
      deep: true,
      handler: function(newValue, oldValue) {
        if (!newValue) {
          this.addCostId = 0;
          this.addCostType = "";
          this.addCostAmount = "";
          this.addCostTypeValue = "";
        }
      }
    }
  },
  methods: {
    getPickData(list) {
      var data = [];
      for (let index in list) {
        data.push(list[index].codeValue);
      }
      return data;
    },
    // 
    addCostAction() {
      if (
        (this.addCostType == "6" || this.addCostType == "7") &&
        this.isHaveGPS
      ) {
        this.$toast.fail("GPS");
        this.addCostType = "";
        this.addCostTypeValue = "";
        this.addCostAmount = "";
        return;
      }
      const toast = this.$toast.loading({
        duration: 0,
        message: "...",
        forbidClick: true,
        loadingType: "spinner"
      });
      let param = Object.assign({
        id: this.addCostId,
        loanNumber: this.$store.state.loanNumber,
        expenseType: this.addCostType,
        amount: this.addCostAmount
      });

      if (this.addCostType == "" || this.addCostAmount == "") {
        this.$toast.fail("");
        return;
      }
      console.log("", param);
      addCost(param).then(res => {
        toast.clear();
        this.getCostList();
      });
      this.showAddCost = false;
    },
    // 
    delectCost(item) {
      const toast = this.$toast.loading({
        duration: 0,
        message: "...",
        forbidClick: true,
        loadingType: "spinner"
      });
      let param = Object.assign({
        id: item.id
      });
      delCost(param).then(res => {
        toast.clear();
        this.getCostList();
      });
    },
    // 
    selectCosType(val) {
      this.addCostTypeValue = val;
      this.addCostType = this.getKey(val, this.cosTypeList);
      this.showCosType = false;
    },
    getKey(str, list) {
      var id = 0;
      for (let index in list) {
        if (str == list[index].codeValue) {
          id = list[index].codeKey;
          break;
        }
      }
      return id;
    },
    getValue(id, list) {
      var str = 0;
      if (id == 1 && list == this.cosTypeList) {
        return "";
      }
      for (let index in list) {
        if (id == list[index].codeKey) {
          str = list[index].codeValue;
          break;
        }
      }
      return str;
    },
    // 
    selectTerm(val) {
      this.baseInfo.term = val;
      this.showTerm = false;
    },
    selectRepayment(val) {
      console.log(val);
      // 1-2-3-
      this.baseInfo.repaymentMethodName = val;
      if (val == "") {
        this.baseInfo.repaymentMethod = "1";
      } else if (val == "") {
        this.baseInfo.repaymentMethod = "2";
      } else {
        this.baseInfo.repaymentMethod = "3";
      }
      this.showRepaymentMethod = false;
    },
    selectUseMethod(val) {
      this.baseInfo.useMethodValue = val;
      this.baseInfo.useMethod = this.getKey(val, this.useTypeList);
      this.showUseMethod = false;
    },
    toSub() {
      console.log(this.baseInfo.leaseType);
      const toast = this.$toast.loading({
        duration: 0,
        message: "...",
        forbidClick: true,
        loadingType: "spinner"
      });
      if (this.baseInfo.businessModel == "1") {
        this.baseInfo.leaseType = "2";
      } else {
        this.baseInfo.leaseType = "1";
      }

      let param = Object.assign({
        loanNumber: this.$store.state.loanNumber,
        businessModel: this.baseInfo.businessModel,
        leaseType: this.baseInfo.leaseType,
        hasGps: this.baseInfo.hasGps,
        isCs: this.baseInfo.isCs,
        isJjk: this.baseInfo.isJjk,
        totalAmount: this.totalAmount,
        downPaymentAmount: this.baseInfo.downPaymentAmount,
        amount: this.amount,
        term: this.baseInfo.term,
        repaymentMethod: this.baseInfo.repaymentMethod,
        useMethod: this.baseInfo.useMethod,
        rate: this.rate,
        gpsAmount: this.gpsAmount
      });
      console.log(param);
      if (
        this.baseInfo.businessModel == "" ||
        (this.baseInfo.businessModel == "1" &&
          this.baseInfo.leaseType == null) ||
        this.baseInfo.hasGps == null ||
        this.baseInfo.isCs == null ||
        this.baseInfo.isJjk == null ||
        this.totalAmount == 0 ||
        this.baseInfo.downPaymentAmount == 0 ||
        this.amount == 0 ||
        this.baseInfo.term == 0 ||
        this.rate == 0 ||
        this.baseInfo.repaymentMethod == null ||
        this.baseInfo.useMethod == null
      ) {
        this.$toast.fail("");
        return;
      }
      saveBussinessInfo(param).then(res => {
        toast.clear();
        this.$router.back();
      });
    },
    calculateCost() {
      if (this.baseInfo.downPaymentAmount == null) {
        return;
      }
      let param = Object.assign({
        term: this.baseInfo.term,
        rate: this.rate,
        repaymentMethod: this.baseInfo.repaymentMethod,
        downPaymentAmount: this.baseInfo.downPaymentAmount,
        loanNumber: this.$store.state.loanNumber
      });
      console.log(param);
      CalculateCost(param).then(res => {
        console.log(res);
        this.totalAmount = res.data.data.totalAmount;
        this.amount = res.data.data.amount;
        this.gpsAmount = res.data.data.gpsAmount;
        this.rate = res.data.data.rate;
      });
    },
    getBusinessInfo() {
      BusinessInfo({ loanNumber: this.$store.state.loanNumber }).then(res => {
        console.log("BusinessInfo", res);
        this.baseInfo = res.data.data;
        // 1-2-3-
        if (this.baseInfo.repaymentMethod == "1") {
          this.baseInfo.repaymentMethodName = "";
        } else if (this.baseInfo.repaymentMethod == "2") {
          this.baseInfo.repaymentMethodName = "";
        } else if (this.baseInfo.repaymentMethod == "3") {
          this.baseInfo.repaymentMethodName = "";
        }
        if (this.baseInfo.downPaymentAmount == null) {
          this.baseInfo.downPaymentAmount = 0;
        }
        if (this.baseInfo.useMethod) {
          this.baseInfo.useMethodValue = this.getValue(
            this.baseInfo.useMethod,
            this.useTypeList
          );
        }

        this.baseInfo.businessModel = "1";
        // if (this.baseInfo.leaseType == "1") {
        //   this.baseInfo.leaseTypeName = "";
        // } else {
        this.baseInfo.leaseTypeName = "";
        // }
        this.getCostList();
      });
    },
    // 
    getCostList() {
      CostList({ loanNumber: this.$store.state.loanNumber }).then(res => {
        console.log(res);
        this.cosList = res.data.data;

        // GPS GPS
        for (let index in this.cosList) {
          if (
            this.cosList[index].expenseType == "6" ||
            this.cosList[index].expenseType == "7"
          ) {
            this.isHaveGPS = true;
          } else {
            this.isHaveGPS = false;
          }
        }

        this.calculateCost();
      });
    },
    // 
    getCode() {
      // 
      codeList({ codeType: "expenseType" }).then(res => {
        this.cosTypeList = res.data.data;
        codeList({ codeType: "useType" }).then(res => {
          this.useTypeList = res.data.data;
          this.getBusinessInfo();
        });
      });
    }
  },
  mounted() {
    // 
    this.getCode();
  }
};
</script>

<style scoped>
.costBox {
  width: 90%;
  margin: auto;
  box-sizing: border-box;
  overflow: scroll;
  white-space: nowrap;
}
::-webkit-scrollbar {
  width: 0px;
}
.costBox_item {
  font-size: 12px;
  min-width: 130px;
  border: 1px solid #ebedf0;
  box-shadow: 0px 5px 5px #ebedf0;
}
.costBox_item_body {
  color: #333333;
  padding: 10px;
  height: 35px;
}

.costBox_item_action {
  display: flex;
  height: 30px;
}
.showAddCost_title {
  width: 100%;
  text-align: center;
  height: 40px;
  line-height: 40px;
  font-size: 17px;
  font-weight: bold;
  box-shadow: 0px 1px 1px #ebedf0;
}
.showAddCost_body {
  font-size: 14px;
  font-weight: bold;
  padding: 0 5%;
}
.showAddCost_btn {
  display: flex;
  width: 90%;
  margin: 0 auto;
  margin-top: 20px;
}
</style>


