<template>
  <div style="height:100%; margin-bottom: 60px;">
    <div>
      <div style="margin-top:20px">
        <div class="headerAndBtn">
          <div class="header"></div>
          <div style="width:40px;margin-top:18px;" @click="showAddUser = true, isMainUser = true">
            <van-icon color="#ff9900" size="20px" name="add-o" />
          </div>
        </div>
        <div v-for="(item,index) in mainListData" :key="index" @click="toDetail(item)">
          <van-swipe-cell>
            <div class="listItem">
              <div class="itemL">
                <van-icon name="https://b.yzcdn.cn/vant/icon-demo-1126.png" size="20" />
                <div style="margin-left:4px">{{item.customerName}}</div>
                <div style="margin-left:20px">{{item.relationValue}}</div>
              </div>
              <div class="itemR">
                <div>{{item.customerType == "1" ? "" : ""}}</div>
                <van-icon
                  v-if="item.saveFlag == 1"
                  color="#0066FF"
                  style="margin-left:4px"
                  name="checked"
                  size="18"
                />
                <van-icon v-else style="margin-left:4px" name="info-o" size="18" />
              </div>
            </div>
            <template #right>
              <van-button square type="danger" text="" @click="delectClick(item)" />
            </template>
          </van-swipe-cell>
        </div>
      </div>
      <div style="margin-top:20px">
        <div class="headerAndBtn">
          <div class="header"></div>
          <div style="width:40px;margin-top:18px;" @click="showAddUser = true, isMainUser = false">
            <van-icon color="#ff9900" size="20px" name="add-o" />
          </div>
        </div>
        <div v-for="(item,index) in danbaoListData" :key="index" @click="toDetail(item)">
          <van-swipe-cell>
            <div class="listItem">
              <div class="itemL">
                <van-icon name="https://b.yzcdn.cn/vant/icon-demo-1126.png" size="20" />
                <div style="margin-left:4px">{{item.customerName}}</div>
                <div style="margin-left:20px">{{item.relationValue}}</div>
              </div>
              <div class="itemR">
                <div>{{item.customerType == "1" ? "" : ""}}</div>
                <van-icon
                  v-if="item.saveFlag == 1"
                  color="#0066FF"
                  style="margin-left:4px"
                  name="checked"
                  size="18"
                />
                <van-icon v-else style="margin-left:4px" name="info-o" size="18" />
              </div>
            </div>
            <template #right>
              <van-button square type="danger" text="" @click="delectClick(item)" />
            </template>
          </van-swipe-cell>
        </div>
      </div>
    </div>
    <div>
      <van-popup
        v-model="showAddUser"
        :style="{ height: '452px', width: '93%'}"
        get-container="body"
      >
        <div>
          <div class="showAddCost_title van-hairline--bottom"></div>
          <div class="showAddCost_body" style="font-size:10px">
            <van-field
              readonly
              clickable
              is-link
              required
              style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
              label=":"
              placeholder=""
              v-model="addUserInfo.customerRoleValue"
              @click="showRole = true"
            />
            <van-field
              readonly
              clickable
              is-link
              required
              style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
              label=":"
              placeholder=""
              v-model="addUserInfo.customerTypeValue"
              @click="showCustomerTypeAction"
            />
            <van-field
              v-model="addUserInfo.customerName"
              style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
              label=":"
              placeholder=""
            />
            <van-field
              readonly
              clickable
              is-link
              style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
              label=":"
              placeholder=""
              v-model="addUserInfo.relationValue"
              @click="showSelectRelation"
            />
            <van-field
              readonly
              clickable
              is-link
              style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
              label=":"
              placeholder=""
              v-model="addUserInfo.idTypeValue"
              @click="popIdType"
            />
            <van-field
              v-model="addUserInfo.idNum"
              style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
              label=":"
              placeholder=""
            />
          </div>
          <div class="showAddCost_btn van-hairline--top">
            <van-button style="width:50%" @click="showAddUser = false"></van-button>
            <van-button style="width:50%" type="info" @click="addUserAction"></van-button>
          </div>
        </div>
      </van-popup>
      <van-popup
        v-model="showRole"
        position="bottom"
        :style="{ height: '300px', width: '100%'}"
        get-container="body"
      >
        <van-picker
          title
          show-toolbar
          :columns="isMainUser? [''] : ['']"
          @confirm="selectRole"
          @cancel="showRole=false"
        />
      </van-popup>
      <van-popup
        v-model="showCustomerType"
        position="bottom"
        :style="{ height: '300px', width: '100%'}"
        get-container="body"
      >
        <van-picker
          title
          show-toolbar
          :columns="customerList"
          @confirm="selectCustomerType"
          @cancel="showCustomerType = false"
        />
      </van-popup>
      <van-popup
        v-model="showRelation"
        position="bottom"
        :style="{ height: '300px', width: '100%'}"
        get-container="body"
      >
        <van-picker
          title
          show-toolbar
          :columns="relationList"
          @confirm="selectRelation"
          @cancel="showRelation=false"
        />
      </van-popup>

      <van-popup
        v-model="showIdType"
        position="bottom"
        :style="{ height: '300px', width: '100%'}"
        get-container="body"
      >
        <van-picker
          title
          show-toolbar
          :columns="idTypeList"
          @confirm="selectIdType"
          @cancel="showIdType=false"
        />
      </van-popup>
    </div>
    <div class="subBtn">
      <van-button class="subBtn_body" block type="info" @click="toSub"> </van-button>
    </div>
  </div>
</template>

<script>
import { userList, addUser, delUser } from "../../request/api";
import { idNumValidator } from "../../utils/common";
export default {
  data() {
    return {
      addUserInfo: {
        loanNumber: this.$store.state.loanNumber, //
        customerName: "", // 
        customerRole: "", //(1- 2-)
        customerRoleValue: "", // 
        customerType: "", //(1=2=)
        customerTypeValue: "", // 
        relation: "", //
        relationValue: "", // 
        idType: "", // 
        idTypeValue: "", // 
        idNum: "", //
        createdBy: "zxc", //
        createdTime: "2020-07-08" // 
      },
      isMainUser: false,
      licenseType: "", //1- 2-
      customerList: [],
      addName: "",
      showAddUser: false,
      showRole: false,
      showRelation: false,
      relationList: [],
      showCustomerType: false,
      showIdType: false,
      idTypeList: [],
      mainListData: [],
      danbaoListData: []
    };
  },
  watch: {
    showAddUser: {
      deep: true,
      handler: function(newValue, oldValue) {
        if (!newValue) {
          console.log("");
          this.addUserInfo = {
            loanNumber: this.$store.state.loanNumber, //
            customerName: "", // 
            customerRole: "", //(1- 2-)
            customerRoleValue: "", // 
            customerType: "", //(1=2=)
            customerTypeValue: "", // 
            relation: "", //
            relationValue: "", // 
            idType: "", // 
            idTypeValue: "", // 
            idNum: "", //
            createdBy: "", //
            createdTime: "" // 
          };
        }
      }
    }
  },
  methods: {
    popIdType() {
      if (this.addUserInfo.customerType == "") {
        this.$toast.fail("");
        return;
      }
      if (this.addUserInfo.customerType == "1") {
        this.idTypeList = [""];
      } else {
        this.idTypeList = [""];
      }
      this.showIdType = true;
    },
    showSelectRelation() {
      if (this.isMainUser) {
        this.relationList = [""];
      } else {
        console.log(this.addUserInfo.customerType);
        if (
          this.addUserInfo.customerType == "" ||
          this.addUserInfo.customerRole == ""
        ) {
          this.$toast.fail("");
          return;
        }
        if (this.addUserInfo.customerType == "1") {
          this.relationList = [
            "",
            "",
            "",
            "",
            "",
            ""
          ];
        } else {
          this.relationList = ["", "", "", "", ""];
        }
      }

      this.showRelation = true;
    },
    toDetail(item) {
      if (item.customerType == "1") {
        this.$router.push({
          name: "userInfoDetail",
          params: {
            customerId: item.customerId
          }
        });
      } else {
        this.$router.push({
          name: "maleBrandInfoDetail",
          params: {
            customerId: item.customerId
          }
        });
      }
    },
    toSub() {

      if(this.mainListData.length == 0){
        this.$toast.fail("");
        return
      }
      this.$router.back();
    },
    showCustomerTypeAction() {
      if (this.isMainUser) {
        if (this.licenseType == "1") {
          this.customerList = [""];
        } else {
          // 
          this.customerList = ["",""];
        }
      } else {
        this.customerList = ["", ""];
      }
      this.showCustomerType = true;
    },
    selectIdType(val) {
      this.addUserInfo.idTypeValue = val;
      if (val == "") {
        this.addUserInfo.idType = "1";
      } else {
        this.addUserInfo.idType = "2";
      }
      this.showIdType = false;
    },
    selectRole(val) {
      // 1- 2-
      if (val == "") {
        this.addUserInfo.customerRoleValue = val;
        this.addUserInfo.customerRole = "1";
      } else {
        this.addUserInfo.customerRoleValue = val;
        this.addUserInfo.customerRole = "2";
      }
      this.showRole = false;
    },

    selectRelation(val) {
      console.log(val);
      if (this.addUserInfo.customerType == "1") {
        // 1- 2- 3- 4- 5- 6- 7-
        if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "1";
        } else if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "2";
        } else if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "3";
        } else if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "4";
        } else if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "5";
        } else if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "6";
        } else if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "7";
        }
      } else {
        // ["", "", "", "", ""];
        if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "1";
        } else if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "2";
        } else if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "3";
        } else if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "4";
        } else if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "5";
        } else if (val == "") {
          this.addUserInfo.relationValue = val;
          this.addUserInfo.relation = "6";
        }
      }

      this.showRelation = false;
    },
    selectCustomerType(val) {
      // 1- 2-
      if (val == "") {
        this.addUserInfo.customerTypeValue = val;
        this.addUserInfo.customerType = "1";
      } else {
        this.addUserInfo.customerTypeValue = val;
        this.addUserInfo.customerType = "2";
      }
      this.showCustomerType = false;
    },
    addUserAction() {
      if (
        this.addUserInfo.customerRole == "" ||
        this.addUserInfo.customerType == ""
      ) {
        this.$toast.fail("");
        return;
      }
      if (this.addUserInfo.idType == "1") {
        if (
          this.addUserInfo.idNum.length > 0 &&
          !idNumValidator(this.addUserInfo.idNum)
        ) {
          this.$toast.fail("");
          return;
        }
      }
      console.log(this.addUserInfo);
      addUser(this.addUserInfo).then(res => {
        console.log(res);
        this.getUserList();
        this.showAddUser = false;
      });
    },
    delectClick(item) {
      console.log(item);
      delUser({ customerId: item.customerId }).then(res => {
        this.getUserList();
      });
    },

    getUserList() {
      var params = Object.assign({ loanId: this.$store.state.loanNumber });
      userList(params).then(res => {
        console.log(res.data);
        this.licenseType = res.data.data.licenseType;
        console.log(this.licenseType);
        let users = res.data.data.customerInfoList;
        this.mainListData = [];
        this.danbaoListData = [];
        for (let item in users) {
          let index = users[item].relation;
          var relationValue = "";
          if (item.customerType == "1") {
            switch (parseInt(index)) {
              case 1: {
                relationValue = "";
                break;
              }
              case 2: {
                relationValue = "";
                break;
              }
              case 3: {
                relationValue = "";
                break;
              }
              case 4: {
                relationValue = "";
                break;
              }
              case 5: {
                relationValue = "";
                break;
              }
              case 6: {
                relationValue = "";
                break;
              }
              case 7: {
                relationValue = "";
                break;
              }
            }
          }else{
            switch (parseInt(index)) {
              case 1: {
                relationValue = "";
                break;
              }
              case 2: {
                relationValue = "";
                break;
              }
              case 3: {
                relationValue = "";
                break;
              }
              case 4: {
                relationValue = "";
                break;
              }
              case 5: {
                relationValue = "";
                break;
              }
              case 6: {
                relationValue = "";
                break;
              }
              case 7: {
                relationValue = "";
                break;
              }
            }
          }

          users[item].relationValue = relationValue;
          if (users[item].customerRole == "1") {
            this.mainListData.push(users[item]);
          } else {
            this.danbaoListData.push(users[item]);
          }
        }
      });
    }
  },
  mounted() {
    this.getUserList();
  }
};
</script>
<style scoped>
.title {
  width: 90%;
  padding: 10px 5%;
  font-size: 16px;
  margin-top: 20px;
}
.listItem {
  background: #f0f0f0;
  border: 1px solid #ebedf0;
  width: 86%;
  height: 45px;
  font-size: 14px;
  margin: 0 auto;
  box-shadow: 0px 5px 5px #ebedf0;
  margin-top: 15px;
  display: flex;
}

.itemL {
  width: 60%;
  margin: 5%;
  display: flex;
  align-items: center;
}
.itemR {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}
.tips {
  font-size: 12px;
  padding: 0 12%;
  text-align: center;
  margin-top: 30px;
}
.subBtn {
  position: fixed;
  bottom: 20px;
  width: 90%;
  left: 5%;
}
.showAddCost_title {
  width: 100%;
  text-align: center;
  height: 40px;
  line-height: 40px;
  font-size: 17px;
  font-weight: bold;
  box-shadow: 0px 1px 1px #ebedf0;
}
.showAddCost_body {
  font-size: 12px;
  font-weight: bold;
  padding: 0 3%;
}
.showAddCost_btn {
  display: flex;
  width: 100%;
  margin-top: 8px;
}
</style>


