import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'

import exhibitionList from '@/views/carInfo/exhibitionList.vue'

// 
import main from '@/views/Home/main.vue'
// 
import menu from '@/views/Menu/menu.vue'
// 
import carInfo from '@/views/carInfo/carInfo.vue'
// 
import carModels from '@/views/carInfo/carModels.vue'
// 
import carModelList from '@/views/carInfo/carModelList'
// 
import carsList from '@/views/carInfo/carsList'
// 
import search from '@/views/Search/search.vue'
// 
import mine from '@/views/Mine/mine.vue'
// 
import setup from '@/views/Mine/setup'
// 
import getphonecode from '@/views/Mine/getphonecode'
// 
import setnewpassword from '@/views/Mine/setnewpassword'


// 
import business from '@/views/BusinessInfo/businessInfo'

// 
import userInfo from '@/views/UserInfo/userInfo'
// 
import userInfoDetail from '@/views/UserInfo/userInfoDetail'
// 
import maleBrandInfoDetail from '@/views/UserInfo/maleBrandInfoDetail'

// 
import selectProduct from '@/views/Product/selectProduct'
// 
import productList from '@/views/Product/productList'
// -
import reimbursementPlan from '@/views/Product/reimbursementPlan'

// 
import uploadFile from '@/views/UploadFile/uploadFile'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/menu',
      name:'menu',
      component:menu,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path: '/search',
      name: 'search',
      component: search,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path: '/mine',
      name: 'mine',
      component: mine,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path: '/setup',
      name: 'setup',
      component: setup,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path: '/getphonecode',
      name: 'getphonecode',
      component: getphonecode,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path: '/setnewpassword',
      name: 'setnewpassword',
      component: setnewpassword,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path: '/',
      name: 'main',
      component: main,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path:'/carInfo',
      name:'carInfo',
      component:carInfo,
      meta:{
        keepAlive:true,
        title: ''
      }
    },
    {
      path:'/exhibitionList',
      name:'exhibitionList',
      component:exhibitionList,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path:'/carModels',
      name:'carModels',
      component:carModels,
      meta:{
        keepAlive:true,
        title: ''
      }
    },
    {
      path:'/carModelList',
      name:'carModelList',
      component:carModelList,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path:'/carsList',
      name:'carsList',
      component:carsList,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path:'/business',
      name:'business',
      component:business,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path:'/userInfo',
      name:'userInfo',
      component:userInfo,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path:'/userInfoDetail',
      name:'userInfoDetail',
      component:userInfoDetail,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path:'/maleBrandInfoDetail',
      name:'maleBrandInfoDetail',
      component:maleBrandInfoDetail,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path:'/selectProduct',
      name:'selectProduct',
      component:selectProduct,
      meta:{
        keepAlive:true,
        title: ''
      }
    },
    {
      path:'/reimbursementPlan',
      name:'reimbursementPlan',
      component:reimbursementPlan,
      meta:{
        keepAlive:false,
        title: '-'
      }
    },
    {
      path:'/productList',
      name:'productList',
      component:productList,
      meta:{
        keepAlive:false,
        title: ''
      }
    },
    {
      path:'/uploadFile',
      name:'uploadFile',
      component:uploadFile,
      meta:{
        keepAlive:false,
        title: ''
      }
    },

  ]
})



