<template>
  <div>
    <div class="section">
      
      <br />130****0099
    </div>
    <van-field v-model="sms" center clearable label="" placeholder="">
      <template #button>
        <van-button size="small" type="primary"></van-button>
      </template>
    </van-field>
    <div style="padding:30px">
      <van-button type="primary" size="large" color="#333333" @click="next"></van-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sms: ""
    };
  },
  methods: {
    next() {
      this.$router.push({
        path: "/setnewpassword"
      });
    }
  }
};
</script>

<style scoped>
.section {
  background: #dfdfdf;
  height: 75px;
  color: #999999;
  font: 10px;
  padding-top: 15px;
  padding-left: 15px;
}
</style>


